import React from 'react';

export default class WordImg extends React.Component{
    constructor(){
        super()
        this.state={
            newsData:"",
            content:[
                {item:'内容一'},
                {item:'内容二'},
                {item:'内容三'}
            ],
        }




    }
    render(){

        var newdateArr = ""
        $.ajax({
            url:"/api/user/signup",

            type:"get",
            async:'false'
        }).done(function(data){

           console.log( $.type(data))
            newdateArr = data

        })

        return(
            <div className="newsImg">
                {newdateArr[0]}
            </div>
        )
    }
}


